gsap.registerPlugin(ScrollTrigger);

// --- 1. CORE SETTINGS & UTILS ---
const isTouch = ('ontouchstart' in window) || (navigator.maxTouchPoints > 0);

// Initialize Lenis (Smooth Scroll)
const lenis = new Lenis({
    duration: 1.5,
    smooth: true,
    smoothTouch: true, // スマホでも慣性スクロールを有効化
    touchMultiplier: 1.5, // スマホでのスクロール感度調整
    // スクロールを制御しない要素を指定
    ignore: (el) => el.hasAttribute('data-lenis-ignore')
});
function raf(time) { lenis.raf(time); requestAnimationFrame(raf); }
requestAnimationFrame(raf);
lenis.on('scroll', ScrollTrigger.update);
gsap.ticker.add((time) => { lenis.raf(time * 1000); });

document.querySelectorAll('[data-lenis-ignore]').forEach((el) => {
    el.addEventListener('wheel', (e) => e.stopPropagation(), { passive: false });
});


// --- 2. UI INTERACTION (CURSOR & MAGNETIC) ---
const cursor = document.querySelector('.c-cursor');
const cursorText = document.querySelector('.c-cursor__text');
const hoverReveal = document.querySelector('.c-hover-reveal');
const hoverRevealImg = document.querySelector('.c-hover-reveal__img');
const magneticCircle = document.querySelector('.c-main-logo__circle');

if (!isTouch) {
    // ... (既存のPC用カーソル処理は変更なし) ...
    document.addEventListener('mousemove', (e) => {
        gsap.to(cursor, { x: e.clientX, y: e.clientY, duration: 0.1 });
        gsap.to(hoverReveal, { x: e.clientX, y: e.clientY, duration: 0.5 });
        const moveX = (e.clientX - window.innerWidth / 2) * 0.05;
        const moveY = (e.clientY - window.innerHeight / 2) * 0.05;
        if(magneticCircle) gsap.to(magneticCircle, { x: moveX, y: moveY, duration: 1, ease: 'power2.out' });
    });

    document.querySelectorAll('.magnetic').forEach(el => {
        el.addEventListener('mousemove', (e) => {
            const rect = el.getBoundingClientRect();
            const x = e.clientX - rect.left - rect.width / 2;
            const y = e.clientY - rect.top - rect.height / 2;
            gsap.to(el, { x: x * 0.3, y: y * 0.3, duration: 0.3, ease: 'power2.out' });
        });
        el.addEventListener('mouseleave', () => {
            gsap.to(el, { x: 0, y: 0, duration: 0.5, ease: 'elastic.out(1, 0.3)' });
        });
    });

    document.querySelectorAll('.hover-target').forEach(el => {
        el.addEventListener('mouseenter', () => {
            cursor.classList.add('is-active');
            cursorText.textContent = el.getAttribute('data-cursor') || '';
            if (el.getAttribute('data-img')) {
                hoverRevealImg.src = el.getAttribute('data-img');
                gsap.to(hoverReveal, { autoAlpha: 1, duration: 0.3 });
                gsap.fromTo(hoverRevealImg, { scale: 1.2 }, { scale: 1, duration: 0.4 });
            }
        });
        el.addEventListener('mouseleave', () => {
            cursor.classList.remove('is-active');
            cursorText.textContent = '';
            gsap.to(hoverReveal, { autoAlpha: 0, duration: 0.3 });
        });
    });
}


// --- 3. OPENING ANIMATION & TEXT SPLIT FIX ---
const setupOpening = () => {
    const splashCurtain = document.getElementById('splashCurtain'); // .c-splash-curtain
    const loadingProgress = document.getElementById('loadingProgress'); // .c-splash-progress__indicator
    const splashProgress = document.getElementById('splashProgress'); // .c-splash-progress

    // --- <br>タグ対応のテキスト分割ロジック ---
    document.querySelectorAll('.u-char-reveal').forEach(el => {
        // innerHTMLを取得して、<br>を一時的なマーカーに置換する
        // 改行コードや余分な空白をトリムしつつ処理
        let html = el.innerHTML;

        // <br>タグを特定の文字列に置換
        const brMark = '##BR##';
        html = html.replace(/<br\s*\/?>/gi, brMark);

        // テキストのみを取り出して分割処理
        const tmpDiv = document.createElement('div');
        tmpDiv.innerHTML = html;
        const textContent = tmpDiv.textContent.trim(); // 余分な空白削除

        // 文字ごとに分割（マーカーはそのまま保持）
        // ここでは簡易的に、一文字ずつspanで囲むが、マーカーは一塊として扱う
        let newHtml = '';
        const chars = Array.from(textContent); // サロゲートペア対策でArray.from推奨

        // マーカー検出用のバッファ
        let buffer = '';

        // 実際の文字再構築（簡易版：本来は正規表現でsplitしたほうが安全だが、今回は簡易実装）
        // もっと単純に、全文字splitした後、マーカー文字列を結合して<br>に戻す方式を採用

        // 一旦、マーカーも含めて1文字ずつ分解されるので、結合ロジックで対応
        // または、もっとシンプルに Node を走査する方法を採用します（推奨）

        el.innerHTML = ''; // 中身をクリア

        // 再帰的にノードを処理して、テキストノードなら分割、BRならそのまま追加
        const splitNode = (node) => {
            if (node.nodeType === Node.TEXT_NODE) {
                const text = node.textContent;
                // 空白のみの場合はそのままスペース、それ以外は分割
                const splitText = text.split('').map(char => {
                    if(char.match(/\s/)) return ' ';
                    return `<span style="display:inline-block; opacity:0; transform:translateY(100%)">${char}</span>`;
                }).join('');
                // spanタグを含む文字列をHTMLとして追加（insertAdjacentHTML等は使えないので一時要素経由）
                const spanWrapper = document.createElement('span');
                spanWrapper.innerHTML = splitText;
                // spanWrapperの中身をelに追加（spanWrapper自体は不要）
                while(spanWrapper.firstChild) {
                    el.appendChild(spanWrapper.firstChild);
                }
            } else if (node.nodeName === 'BR') {
                el.appendChild(document.createElement('br'));
            } else {
                // 他のタグがある場合（入れ子）、その中身も再帰処理
                const clone = node.cloneNode(false);
                el.appendChild(clone);
                Array.from(node.childNodes).forEach(child => splitNode(child, clone));
            }
        };

        // 元のHTML文字列からDOMParserでノード化して処理
        const parser = new DOMParser();
        const doc = parser.parseFromString(html.replace(/##BR##/g, '<br>'), 'text/html');
        Array.from(doc.body.childNodes).forEach(node => splitNode(node));
    });

    // アニメーションの適用（変更なし）
    document.querySelectorAll('.u-char-reveal span').forEach(span => {
            // GSAP ScrollTrigger設定を個別に適用、あるいは親要素トリガーでstaggerさせる
    });

    // 親要素単位でトリガーを設定して、子要素のspanを動かす方が効率的かつレイアウト崩れが起きにくい
    document.querySelectorAll('.u-char-reveal').forEach(el => {
        gsap.to(el.querySelectorAll('span'), {
            scrollTrigger: { trigger: el, start: 'top 85%' },
            y: 0, opacity: 1, duration: 1, stagger: 0.03, ease: 'power4.out'
        });
    });

    // --- OPアニメーション ---
    const introTl = gsap.timeline({
        onComplete: () => {
            splashCurtain.style.pointerEvents = 'none';
            document.body.style.overflow = 'auto';
        }
    });

    introTl
        .to(loadingProgress, { width: '100%', duration: 1.2, ease: 'power2.inOut' })
        .to('.c-main-logo__path', { y: 0, opacity: 1, duration: 1, stagger: 0.05, ease: 'power4.out' }, '<')
        .to(splashProgress, { opacity: 0, duration: 0.2 })
        .to('.c-main-logo__circle', { opacity: 1, scale: 1, duration: 0.8, ease: 'back.out(1.7)' }, '-=0.5')
        .to(splashCurtain, { clipPath: 'inset(0 0 100% 0)', duration: 1.2, ease: 'power4.inOut' }, 'wipe')
        .to('.l-header', { y: 0, duration: 0.8, ease: 'power3.out' }, 'wipe+=0.5')
        .to('.p-hero__meta', { opacity: 1, duration: 1 }, 'wipe+=0.8');
};


// --- 4. MENU SYSTEM ---
const setupMenu = () => {
    const menuBtn = document.getElementById('menuBtn'); // .l-header__menu-trigger
    const menuOverlay = document.getElementById('menuOverlay'); // .p-global-menu
    const menuLinks = document.querySelectorAll('.p-global-menu__nav-link');
    const bgImages = document.querySelectorAll('.p-global-menu__bg-img');
    let isMenuOpen = false;


    const menuTl = gsap.timeline({ paused: true, onReverseComplete: () => gsap.set(menuOverlay, { visibility: 'hidden' }) });

    menuTl
        .set(menuOverlay, { visibility: 'visible' })
        .fromTo('.p-global-menu__shutter', { y: '100%' }, { y: '0%', duration: 0.6, ease: 'power3.inOut' })
        .to('.p-global-menu__shutter', { y: '-100%', duration: 0.6, ease: 'power3.inOut' })
        .fromTo('.p-global-menu__bg', { opacity: 0 }, { opacity: 1, duration: 0.1 }, "-=0.3") // Previously .menu-bg-layer
        .to(menuLinks, { y: 0, duration: 0.8, stagger: 0.05, ease: 'power4.out' }, "-=0.4")
        .to('.p-global-menu__sidebar', { opacity: 1, duration: 0.8 }, "<");

    const toggleMenu = () => {
        isMenuOpen = !isMenuOpen;
        if (isMenuOpen) {
            menuBtn.textContent = "CLOSE";
            menuTl.play();
            lenis.stop(); // メニュー展開時は背景スクロール停止
        } else {
            menuBtn.textContent = "MENU";
            menuTl.timeScale(1.5).reverse();
            lenis.start();
        }
    };

    menuBtn.addEventListener('click', toggleMenu);

    menuLinks.forEach(link => {
        link.addEventListener('click', (e) => {
            e.preventDefault();
            const targetId = link.getAttribute('href');
            toggleMenu();
            if (targetId && targetId !== '#') {
                lenis.scrollTo(targetId, { offset: 0, duration: 2, easing: (t) => 1 - Math.pow(1 - t, 4) });
            }
        });

        if(!isTouch) {
            link.addEventListener('mouseenter', () => {
                bgImages.forEach(img => img.classList.remove('is-active')); // Previously .active
                const target = document.getElementById(link.getAttribute('data-bg'));
                if(target) target.classList.add('is-active'); // Previously .active
            });
        }
    });

    if(!isTouch) {
        document.querySelector('.p-global-menu__nav')?.addEventListener('mouseleave', () => { // Previously .menu-nav
            bgImages.forEach(img => img.classList.remove('is-active')); // Previously .active
            document.getElementById('bg-default').classList.add('is-active'); // Previously .active
        });
    }
};


// --- 5. WORKFLOW ANIMATION ---
const setupWorkflow = () => {
    const timeline = document.querySelector('.p-workflow__timeline'); // Previously .workflow-timeline
    const progress = document.querySelector('.p-workflow__timeline-progress'); // Previously .timeline-progress

    if (timeline && progress) {
        gsap.fromTo(progress, { height: '0%' }, {
            height: '100%', ease: 'none',
            scrollTrigger: { trigger: timeline, start: 'top center', end: 'bottom center', scrub: true }
        });
    }

    document.querySelectorAll('.p-workflow__item').forEach(item => { // Previously .timeline-item
        ScrollTrigger.create({
            trigger: item, start: 'top 60%',
            onEnter: () => item.classList.add('is-active'), // Previously .active
            onLeaveBack: () => item.classList.remove('is-active') // Previously .active
        });
    });
};


// --- 6. SCROLL EFFECTS (Fixes for SP) ---
const setupScrollEffects = () => {

    // Philosophy Cinematic: SPでもアニメーションを適用（PCと全く同じ挙動にする）
    const cinematicTl = gsap.timeline({
        scrollTrigger: {
            trigger: '.p-philosophy', // Previously .philosophy-cinematic
            start: 'top top',
            end: '+=200%',
            pin: true, // SPでもPinする
            scrub: 1,
            // SPでのアドレスバー伸縮によるガタつき防止
            pinType: isTouch ? 'fixed' : 'transform'
        }
    });
    cinematicTl
        .to('.p-philosophy__box', { width: '100vw', height: '100dvh', duration: 1, ease: 'power2.inOut' }) // Previously .philo-box
        .to('.p-philosophy__img-inner img', { filter: 'brightness(0.4)', duration: 0.5 }, '>-0.3') // Previously .philo-img-inner img
        .to('.p-philosophy__label', { opacity: 0.8, duration: 0.4 }, '<') // Previously .philo-label
        .to('.p-philosophy__title', { opacity: 1, y: 0, duration: 0.6 }, '-=0.2') // Previously .philo-main-title
        .to('.u-line-reveal', { opacity: 1, y: 0, duration: 0.8, stagger: 0.2 }, '-=0.4'); // Previously .philo-line-reveal


    // Horizontal Scroll: SPでも有効化 (if文での除外を削除)
    const hSection = document.querySelector('.p-snapshots'); // Previously .horizontal-section
    const hContainer = document.querySelector('.p-snapshots__container'); // Previously .horizontal-container

    if (hSection && hContainer) {
        // ScrollTrigger.matchMediaを使っても良いが、今回はシンプルに全デバイスで有効化

        // コンテナの実際の幅を取得（SPでは狭くなるがロジックは同じ）
        const getScrollAmount = () => {
            let sectionWidth = hContainer.scrollWidth;
            let wrapperWidth = window.innerWidth;
            return -(sectionWidth - wrapperWidth);
        };

        const tween = gsap.to(hContainer, {
            x: getScrollAmount, // 関数を渡してリサイズ時に再計算させる
            ease: "none",
            scrollTrigger: {
                trigger: hSection,
                pin: true,
                scrub: 1,
                start: "top top",
                end: () => "+=" + (hContainer.scrollWidth - window.innerWidth), // スクロール量はコンテンツ幅に比例
                invalidateOnRefresh: true // リサイズ時の再計算
            }
        });
    }

    // Parallax
    gsap.utils.toArray('.js-parallax img').forEach(img => {
        gsap.to(img, {
            yPercent: 20, ease: 'none',
            scrollTrigger: { trigger: img.parentElement, start: 'top bottom', end: 'bottom top', scrub: true }
        });
    });

    // BG Morph
    const morphTl = gsap.timeline({ scrollTrigger: { trigger: "body", start: "top top", end: "bottom bottom", scrub: 1 } });
    morphTl.to('.c-bg-shape-container__path', { // Previously .morph-path:not(.menu-morph-path)
        attr: { d: "M10,50 Q30,20 50,50 T90,50 T50,90 T10,50" },
        rotation: 360, scale: 1.5, transformOrigin: "center center", ease: "none"
    })
    .to('.c-bg-shape-container', { opacity: 0.1, ease: "none" }, "<"); // Previously .bg-shape-container

    // Initial shape
    document.querySelector('.c-bg-shape-container__path').setAttribute('d', "M10,50 Q30,20 50,50 T90,50 T50,90 T10,50"); // Previously .bg-shape-container .morph-path

    // Footer Reveal
    const footerContent = document.querySelectorAll('.l-footer__grid, .l-footer__cta'); // Previously .footer-grid, .footer-cta-section
    if(footerContent.length > 0) {
        gsap.from(footerContent, {
            y: 50, opacity: 0, duration: 1, stagger: 0.1,
            scrollTrigger: { trigger: '#footer', start: 'top 80%', toggleActions: 'play none none reverse' }
        });
    }
};


// --- 7. UTILITIES ---
const setupUtilities = () => {
    const modal = document.getElementById('workModal'); // .p-work-modal
    const modalClose = document.querySelector('.p-work-modal__close'); // Previously .modal-close
    document.querySelectorAll('.p-works__trigger').forEach(trigger => { // Previously .work-trigger
        trigger.addEventListener('click', (e) => {
            e.stopPropagation();
            document.querySelector('.p-work-modal__image').src = trigger.getAttribute('data-image'); // Previously .modal-image
            document.querySelector('.p-work-modal__title').textContent = trigger.getAttribute('data-title'); // Previously .modal-title
            document.querySelector('.p-work-modal__category').textContent = trigger.getAttribute('data-category'); // Previously .modal-category
            document.querySelector('.p-work-modal__description').textContent = trigger.getAttribute('data-desc'); // Previously .modal-description
            modal.classList.add('is-open'); // Previously .open
            lenis.stop();
        });
    });
    if(modalClose) modalClose.addEventListener('click', () => { modal.classList.remove('is-open'); lenis.start(); }); // Previously .open

    document.getElementById('backToTop')?.addEventListener('click', () => { // .c-back-to-top
        lenis.scrollTo(0, { duration: 2, easing: (t) => 1 - Math.pow(1 - t, 4) });
    });

    document.querySelectorAll('.l-footer__links a[href^="#"]').forEach(link => { // Previously .footer-links a[href^="#"]
        link.addEventListener('click', (e) => {
            e.preventDefault();
            const id = link.getAttribute('href');
            if(id && id !== '#') lenis.scrollTo(id, { duration: 2, easing: (t) => 1 - Math.pow(1 - t, 4) });
        });
    });

    // Time & Status
    const updateTime = () => {
        const now = new Date();
        const jst = new Date(now.toLocaleString('en-US', { timeZone: 'Asia/Tokyo' }));
        const day = jst.getDay();
        const mins = jst.getHours() * 60 + jst.getMinutes();
        const isWork = (day !== 0 && day !== 6) && (mins >= 600 && mins <= 1050);

        const timeDisplay = document.getElementById('okinawaTime'); // .l-footer__time-display
        if(timeDisplay) {
            timeDisplay.textContent = now.toLocaleTimeString('en-US', { timeZone: 'Asia/Tokyo', hour12: false, hour: '2-digit', minute: '2-digit' }) + ' JST';
        }
        document.querySelectorAll('.c-status-indicator').forEach(indicator => { // Previously .status-indicator
            const dot = indicator.querySelector('.c-status-indicator__dot'); // Previously .status-dot
            const txt = indicator.querySelector('.c-status-indicator__text'); // Previously .status-text
            if (dot && txt) {
                if (isWork) { dot.classList.add('is-on'); txt.textContent = 'AVAILABLE FOR WORK'; } // Previously .is-on
                else { dot.classList.remove('is-on'); txt.textContent = 'OFFLINE'; } // Previously .is-on
            }
        });
    };
    setInterval(updateTime, 1000);
    updateTime();

    // Noise Canvas
    const canvas = document.getElementById('noiseCanvas'); // .c-noise-canvas
    if(canvas) {
        const ctx = canvas.getContext('2d');
        const resizeNoise = () => { canvas.width = window.innerWidth; canvas.height = window.innerHeight; };
        const drawNoise = () => {
            const w = canvas.width, h = canvas.height;
            const idata = ctx.createImageData(w, h);
            const buffer32 = new Uint32Array(idata.data.buffer);
            for (let i = 0; i < buffer32.length; i++) if (Math.random() < 0.5) buffer32[i] = 0xffffffff;
            ctx.putImageData(idata, 0, 0);
            requestAnimationFrame(drawNoise);
        };
        window.addEventListener('resize', resizeNoise);
        resizeNoise();
        drawNoise();
    }
};

// --- 8. FAQ ACCORDION ---
const setupFaq = () => {
    document.querySelectorAll('.c-faq__item').forEach(detail => { // Previously .faq-item
        const summary = detail.querySelector('.c-faq__summary'); // Previously .faq-summary
        const content = detail.querySelector('.c-faq__answer'); // Previously .faq-content

        summary.addEventListener('click', (e) => {
            e.preventDefault();
            const isOpen = detail.hasAttribute('open');
            if (isOpen) {
                gsap.to(content, {
                    height: 0, opacity: 0, duration: 0.4, ease: 'power3.inOut',
                    onComplete: () => { detail.removeAttribute('open'); ScrollTrigger.refresh(); }
                });
            } else {
                detail.setAttribute('open', 'true');
                gsap.fromTo(content, { height: 0, opacity: 0 }, {
                    height: 'auto', opacity: 1, duration: 0.6, ease: 'power3.out',
                    onComplete: () => ScrollTrigger.refresh()
                });
            }
        });
    });
};

